@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
.dropdown-container {
    visibility: hidden;
    position: absolute;
    right: 15px;
    top: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    max-height: 309px;
    height: auto;
    opacity: 0;
    transform: translate(0%, 100%);
    /* 
    Default:
    width: 233px;
    max-height: 309px;
    height: auto; 
    */

    background: rgba(255, 255, 255, 0.6);

    border: 0.5px solid rgba(0, 0, 0, 0.2);
    -webkit-backdrop-filter: blur(50px);
            backdrop-filter: blur(50px);
    /* Note: backdrop-filter has minimal browser support */

    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);

    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 10px 0;
    overflow: hidden;
    transition: transform 0.25s, visibility 0.25s, opacity 0.25s;
}

.drop-open {
    height: auto;
    opacity: 1;
    visibility: visible;
    transform: translate(0% ,0%);
}

.option {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 2px 20px;
    width: 100%;
    /* Inside Auto Layout */
    flex: none;
    order: 4;
    flex-grow: 0;
    margin: 4px 0px;
    cursor: default;
}
.option:hover {
    color: #fff;
    background: #3a6cd9;
}

.App.dark .dropdown-container {
    background: rgba(40, 40, 40, 0.6);
    border-color: rgba(255, 255, 255, 0.1);
}

.App.dark .option {
    color: #fafafa;
}
* {
  padding: 0;
  margin: 0;
  font-family: "Inter", "SF Pro Text", sans-serif;
  box-sizing: border-box;
}

*::selection {
  background: #3a6cd950;
}

::-webkit-scrollbar {
  display: none;
}

.App {
  padding: 5px;
  min-height: 100vh;
  min-height: 100vh;
  width: 100vw;
  font-family: "Inter", sans-serif;
  background: #ebebeb;
  transition: background 0.35s;         
}

textarea {
  resize: none;
}

.download-container {
  text-align: center;
}

#btn-download {
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 2%;
  min-width: 10rem;
  height: 4rem;
  outline: none;
  border: none;
  background:#3a6cd9;
  transition: transform 0.35s;
}

#btn-download:active {
  transform: scale(0.95);
}

.App.dark #btn-download {
  color: #eeeeee;
}

.App.dark {
  background: #191919;
}

/* Changing the readme dark-mode styles too */
/* ---START--- */

.App.dark pre, .App.dark table {
  background: #1E1E1E;
}

/* ---END--- */

.workspace {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  min-height: 90vh;
  max-width: 100vw;
  font-family: "Inter", sans-serif;
}

.editor-window-mac {
  position: relative;
  overflow: hidden;
  width: calc(calc(100vw / 2) - 50px);
  height: calc(100vh / 1.2);
  background: #f6f6f6;
  border: 0.5px solid rgba(0, 0, 0, 0.12);
  -webkit-filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.15)),
    drop-shadow(0px 25px 30px rgba(0, 0, 0, 0.35));
          filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.15)),
    drop-shadow(0px 25px 30px rgba(0, 0, 0, 0.35));
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15) 0px 25px 30px rgba(0, 0, 0, 0.35);
  border-radius: 11px;
  transition: height 0.3s, width 0.3s, visibility 0.1s;
}

.editor-window-mac.dark {
  background: #282828;
  border: 0.5px solid rgba(255, 255, 255, 0.12);
}

.not-visible {
  visibility: hidden;
  display: none;
}

.fullscreen {
  height: 100vh;
  width: 100vw;
}

.editor-window-mac .top-bar {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 20px;
  /* padding-right: calc(calc(50% / 2) - 30px); */
  width: 100%;
  height: 43px;
  background: #fff;
  box-shadow: 0px 0.5px 0px rgba(0, 0, 0, 0.15),
    inset 0px -0.5px 0px rgba(0, 0, 0, 0.05);
  border-radius: 10px 10px 0px 0px;
}

.editor-window-mac .top-bar.dark {
  background: #1E1E1E;
}

.editor-window-mac .top-bar.preview {
  justify-content: space-between;
}

.editor-window-mac .top-bar #add-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.editor-window-mac .top-bar.dark #add-icon path {
  fill: #999C9F;
}

.editor-window-mac .top-bar #add-icon:hover path {
  fill: #3a6cd9;
}

.editor-window-mac .top-bar input {
  overflow: hidden;
  min-width: 70%;
  text-align: center;
  line-height: 1.5rem;
  height: 1.5rem;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.05);
  font-size: 13px;
  border-radius: 6px;
  outline: none;
  border: none;
  transition: box-shadow 0.35s;
}

.editor-window-mac .top-bar.dark input {
  background: #0C0F12;
  color: #fafafa;
}

/* box-shadow of input element #3a6cd9 */

.editor-window-mac .top-bar input:focus {
  box-shadow: 0 0 0 2.5px rgba(51, 113, 247, 0.5);
}

.editor-window-mac .top-bar.dark input:focus {
  box-shadow: rgba(51, 113, 247, 0.5);
}

.editor-window-mac .top-bar p {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 70%;
  line-height: 1.5rem;
  height: 1.5rem;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.05);
  font-size: 13px;
  border-radius: 6px;
  outline: none;
}

.editor-window-mac .top-bar.dark p {
  background: #0C0F12;
  color: #fafafa;
}

.icons {
  height: 100%;
  width: 70px;
  margin-left: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.icon {
  height: 12px;
  width: 12px;
  border-radius: 100px;
  border: 0.5px solid rgba(0, 0, 0, 0.12);
}

.icon.close {
  background: #ff5e57;
}

.icon.minimize {
  background: #ffbb2e;
}

.icon.maximize {
  cursor: pointer;
  background: #38c149;
}

.editor {
  font-family: "Inter", "SF Mono", sans-serif;
  font-size: 18px;
}

.editor,
.preview {
  height: 100%;
  max-height: 90%;
  width: 100%;
  overflow: auto;
  outline: none;
  background: none;
  border: none;
  padding: 5px 10px;
}

.App.dark .editor,
.App.dark .preview {
  color: #ebebeb;
}

.editor-window-mac #preview {
  max-height: 100%;
  padding: 10px 20px 40px 20px;
}

.switch-container {
  max-width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
}

.switch-container p {
  color: #353637;
}

.switch-container p strong {
  color: #171718;
}

.App.App.dark .switch-container p {
  color: #c5c5c5;
  transition: color 0.3s;
}

.App.App.dark .switch-container p strong {
  color: #fafafa;
  transition: color 0.3s;
}

.theme-toggle {
  display: flex;
  align-items: center;
  padding: 3px;
  overflow: hidden;
  height: calc(22px * 1.15);
  width: calc(40px * 1.15);
  border-radius: 100px;
  background: #999C9F;
  box-shadow: 0px 4px 4px 0px #000000 5% inset;
  transition: background 0.35s;
}

.theme-toggle.enabled {
  background: #007AFF;
}

.theme-toggle .switch {
  margin-top: 0.5px;
  height: 20px;
  width: 20px;
  background: #FFFFFF;
  box-shadow: 0px 0.5px 4px 0px #000000 20%;
  border-radius: 100px;
  cursor: pointer;
  transition: transform 0.35s;
}

.theme-toggle .switch.enabled {
  transform: translateX(100%);
}

@media screen and (max-width: 550px) {
  .workspace {
    flex-direction: column;
    min-height: 100vh;
  }

  .editor-window-mac {
    width: 90%;
    height: calc(calc(100vh / 2) - 20px);
  }

  .editor {
    max-height: 80%;
  }

  .editor-window-mac .top-bar {
    padding-right: calc(calc(50% / 2) - 58px);
  }

  .editor-window-mac .top-bar p {
    font-size: 11px;
  }
  
  .not-visible {
    visibility: hidden;
    display: none;
  }

  .fullscreen {
    height: 100vh;
    width: 100vw;
  }
  .editor-window-mac .top-bar.preview {
    justify-content: space-around;
    padding-right: calc(calc(50% / 2) - 40px);
  }
}

@media screen and (max-height: 400px) and (orientation: landscape) {
  .editor {
    max-height: 70%;
  }
}

@media screen and (width: 620px) {
  .editor-window-mac .top-bar {
    padding-right: calc(calc(50% / 2) - 40px);
  }
}
img {
    max-width: 100%;
    height: auto;
    margin: 10px 0;
    border-radius: 10px;
}

pre {
    background: #fff;
    padding: 10px;
    border-radius: 10px;
    margin: 5px 0;
}

pre.dark {
    background: #0C0F12;
    color: #ebebebaa;
}

table {
    padding: 10px;
    height: 30%;
    max-height: 100%;
    width: 100%;
    max-width: 100%;
    background: #fff;
    border-radius: 10px;
}

table thead tr {
    height: 40px;
    width: calc(100% / 4);
}

table thead tr th {
    text-align: center;
    margin: 0 3px;
    font-size: 1.1rem;
}

table tbody tr td {
    text-align: center;
    margin: 0 3px;
    font-size: 0.912rem;
    font-weight: 600;
    color: #575757;
}

.App.dark table tbody tr td {
    color: #adadad;
}

a {
    text-decoration: none;
    color: #166edc;
}

a:hover {
    text-decoration: underline;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
}

ul, ol {
    padding: 5px 10px;
}

h1, h2, h3, h4, h5, h6, p {
    padding: 5px 10px;
}

code {
    display: flex;
    overflow: scroll;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    word-wrap: break-word;
}

input {
    overflow: hidden;
    min-width: 70%;
    text-align: left;
    padding: 0 10px;
    line-height: 1.5rem;
    height: 2rem;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.05);
    font-size: 13px;
    border-radius: 6px;
    outline: none;
    border: none;
    margin: 10px 0;
    transition: box-shadow 0.35s;
}

input:focus {
    box-shadow: 0 0 0 2.5px rgba(51, 113, 247, 0.5);
}

.App.dark input {
    background: #0C0F12;
    color: #fafafa;
}

button {
    overflow: hidden;
    min-width: 70%;
    text-align: center;
    padding: 0 10px;
    line-height: 1.5rem;
    height: 2rem;
    overflow: hidden;
    background: #166edc;
    color: #fafafa;
    cursor: pointer;
    font-size: 13px;
    border-radius: 6px;
    outline: none;
    border: none;
    margin: 10px 0;
    transition: box-shadow 0.35s;
}

button:focus {
    box-shadow: 0 0 0 3.5px rgba(51, 113, 247, 0.3);
}
